@import url(https://fonts.googleapis.com/css2?family=Alfa+Slab+One&display=swap);
*, *::before, *::after {
  box-sizing: border-box;
}

* {
  margin: 0;
}
html, body {
  height: 100%;
  font-size: 16px
}

body {
  line-height: 1.5;
  -webkit-font-smoothing: antialiased;
  font-family: -apple-system, "Segoe UI", "Roboto", "Noto Sans", "Ubuntu", "Cantarell", "Helvetica Neue";
  margin: 0;
}

img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

input, button, textarea, select {
  font: inherit;
}

p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}
.headerCont{
    background-color: #334e68;
}
.titHead{
    font-size: 3rem;
    margin: 0;
    text-align: center;
    font-family: 'Alfa Slab One', cursive;
    color: #000000;
    text-shadow: 2px 0 0 #fff, -2px 0 0 #fff, 0 2px 0 #fff, 0 -2px 0 #fff, 1px 1px #fff, -1px -1px 0 #fff, 1px -1px 0 #fff, -1px 1px 0 #fff;
}
.container{
    background-color: #f0f4f8;
    min-height: 100vh;
}

.winnerAnnouncement{
    background-color: rgba(0, 0, 0, 0.568);
    min-height: 100vh;
    min-width: 100vw;
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
    position: absolute;
    -webkit-justify-content: center;
            justify-content: center;
}

.textWinner{
    margin: 0;
    color: white;
}
.userBoard{
    display: grid;
    grid-template-columns: repeat(10, 35px);
    grid-template-rows: repeat(10, 35px);
}

.agruparBoard{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    grid-gap: 1rem;
    gap: 1rem;
}

.textB{
    padding: 0.5rem 1rem;
    background-color: #54D1DB;
    border-radius: 5px;
    width: 8.125rem;
    height: 2.3rem;
    border: 1px solid #38BEC9;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.tB{
    color: #0E7C86;
    font-size: 0.8rem;
}

@media (max-width: 800px) {
    .tB{
        font-size: 0.7rem;
    }
}

:root{
    --water-col: #d4f1f9;
    --ship-col: #787f86;
    --hit-cool: #43707e;
    --ship-hit: #8b0000;
    --ship-hov: #4c535a;
    --water-hov: #a4c3cc;
}

.boxinboard{
    border: 1px solid rgba(0, 0, 0, 0.486);
}

.water{
    background-color: #d4f1f9;
    background-color: var(--water-col);
    cursor: pointer;
}
.water:hover{
    background-color: #a4c3cc;
    background-color: var(--water-hov);
}

.ship{
    background-color: #787f86;
    background-color: var(--ship-col);
    cursor: pointer;
}
.ship:hover{
    background-color: #4c535a;
    background-color: var(--ship-hov);
}

.hitN{
    background-color: #43707e;
    background-color: var(--hit-cool);
}

.hitS{
    background-color: #8b0000;
    background-color: var(--ship-hit);
}
.titlePlace{
    height: 50px;
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    -webkit-justify-content: center;
            justify-content: center;
    border-radius: 5px;
    padding: 5px;
}

.titPlace{
    margin: 0;
    font-size: 1rem;
    color: #486581;
}
.boardsCont{
    display: -webkit-flex;
    display: flex;
    grid-gap: 4rem;
    gap: 4rem;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-flex-direction: row;
            flex-direction: row;
}
.boardP{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.botonP{
    width: 100px;
    padding: 0.3rem;
    margin: 0.5rem 0.75rem;
    text-transform: capitalize;
    background-color: #38bec9;
    outline: none;
    color: #044E54;
    border: 1px solid #0E7C86;
    border-bottom: 2px solid #0E7C86;
    transition: background-color 0.5s ease-in;
    cursor: pointer;
}

.botonP:hover{
    background-color: #14919B;
}

.botonesT{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
}

.containerWP{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    grid-gap: 1rem;
    gap: 1rem;
}
.placed{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    grid-gap: 1rem;
    gap: 1rem;
}
.containerALL{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-align-items: center;
            align-items: center;
}

.textingGame{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    border-radius: 5px;
    padding: 1rem 0;
}

.textW{
    margin: 0;
    color: #486581;
}

@media (max-width: 900px) {
    .containerWP{
        -webkit-flex-direction: column;
                flex-direction: column;
        -webkit-align-items: center;
                align-items: center;
    }
}
@media (max-width: 800px) {
    .textW{
        font-size: 1rem;
    }
}
